@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}




.header{
    display:flex;
    justify-content: center;
    box-shadow:0px 0 8px -4px #888;
    color:hsl(200, 15%, 8%);
}

.header__content{
    height:80px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    box-sizing:border-box;
    font-family: 'Nunito Sans', sans-serif;
    width:100%;
    max-width: 1420px;
    padding:0px 20px;
    
}


.header h1{
    font-size: 27px;
    font-weight: 800;
}

.header__theme-changer{
    width: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 19px;
    cursor:pointer;
    min-width:120px;
}

/* dark mode */

.header--dark{
    background-color:hsl(209, 23%, 22%);
    color:white !important;
}

@media only screen and (max-width: 476px) {
    .header h1 {
      font-size:20px;
    }
    .header__theme-changer{
        font-size:17px;
        max-width:110px;
        min-width:110px;
    }
  }
.nav{
    display:flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

}

.nav__search{
    background-color:white;
    width: 520px;
    height: 60px;
    display:flex;
    align-items:center;
    box-sizing:border-box;
    padding:10px 5px;
    border-radius: 5px;
    box-shadow:0px 0 8px -4px #888;
    margin: 20px 0px;
}

.nav__search-icon {
    width:50px;
    font-size:25px;
    cursor:pointer;
}


.nav__search-input{
    flex:1 1;
    height:100%;
    border:none;
    outline:none;
    font-size:15px;
    font-family: 'Nunito Sans', sans-serif;
    background-color: transparent;
}

.nav__search-input::-webkit-input-placeholder{
    color:gray;
}

.nav__search-input:-ms-input-placeholder{
    color:gray;
}

.nav__search-input::placeholder{
    color:gray;
}


.nav__category{
    
    border-radius:5px;
    font-weight:600;
    position:relative;
    margin: 20px 0px;
    
}

.category__title{
    width: 220px;
    border-radius: 5px;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0px 15px;
    font-family: 'Nunito Sans', sans-serif;
    cursor:pointer;
    background-color: white;
    box-shadow:0px 0 8px -4px #888;
}

.category__options{
    margin-top:10px;
    border-radius:5px;
    background-color: rgb(255, 255, 255);
    position:absolute;
    width:100%;
    box-shadow:0px 0 8px -4px #888;
}


.category__option{
box-sizing:border-box;
padding:0px 15px;
height:40px;
display:flex;
align-items:center;
justify-content: flex-start;
font-weight: 300;
cursor:pointer;
}

.category__option:hover{
    background-color: rgba(214, 214, 214, 0.363);
}


/* dark mode */


/* search */
.nav__search--dark{
    background-color: hsl(209, 23%, 22%);
    color:white;
}

.nav__search--dark input{
    color:white;
}

.nav__search-input::-webkit-input-placeholder{
    color:rgb(211, 211, 211);
}

.nav__search-input:-ms-input-placeholder{
    color:rgb(211, 211, 211);
}

.nav__search-input::placeholder{
    color:rgb(211, 211, 211);
}

.nav__category--dark .category__title, .nav__category--dark .category__options{
    background-color: hsl(209, 23%, 22%);
    color:white;
}
.main{
    background-color: hsl(0, 0%, 98%);
    display:flex;
    justify-content: center;
    min-height:calc(100vh - 80px);
    
}

.main__content{
    
    width:100%;
    max-width: 1420px;
    box-sizing:border-box;
    padding:20px;
}

.main--dark{
    background-color: hsl(207, 26%, 17%);
}
.catalog__countries{
display:grid;
grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));
grid-gap:40px;
box-sizing:border-box;
padding:30px 0px;
font-family: 'Nunito Sans', sans-serif;
color:hsl(200, 15%, 8%);
}

.catalog__country{
    border-radius: 7px;
    box-shadow:2px 4px 10px -4px #888;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    
}

.country__flag{
width:100%;
border-radius: 7px 7px 0px 0px;
max-height:50%;
}

.country__info{
    height:50%; 
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    justify-content: flex-end;
}
.country__name{
font-size:20px;
font-weight:800;
box-sizing: border-box;
padding: 20px;
}

.country__properties{
    box-sizing: border-box;
    padding: 0px 0px 20px 20px;
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    font-size:14px;
}

.country__property{
    padding:5px 0px;
}

.country__property strong{
    font-weight:800;
}


/* dark mode */

.catalog__country--dark{
    background-color:hsl(209, 23%, 22%);
    color:white;
    box-shadow:2px 4px 10px -4px rgb(27, 27, 27);
}
